import React from 'react';
import { withRouter } from 'react-router-dom';
import Button from '../../../../Input-controls/Button/Button';
import { urls } from '../../../../routes/routes';
import './Banner.scss'

function Banner(props) {
    return (
        <>
        <div className="webview-bnnr">
            <img src="https://res.cloudinary.com/onsurity/image/upload/v1628602212/New-home-screen/Group_7905_y6sptn.svg" width="100%" />
            <div className="get-quotebtn">
               <Button 
                    onClick={()=>props.history.push(urls.VERIFY_PHONE)}
                    className="qoutebtn-styl" 
                    value="Get Quote" />
            </div>
        </div>

        <div className="mobleview-bnnr">
            <img src="https://res.cloudinary.com/onsurity/image/upload/v1628675231/New-home-screen/Group_8007_tkljln.svg" width="100%" height="100%" />
            <div className="get-quotebtn">
               <Button 
                    onClick={()=>props.history.push(urls.VERIFY_PHONE)}
                    className="qoutebtn-styl" 
                    value="Get Quote" />
            </div>
        </div>
    </>
    );
}

export default withRouter(Banner);