import React, { useEffect } from 'react'
import Header from '../../components/Home/HeaderSection/Header'
import Banner from './Components/Banner/Banner'
import EmployeeHealthcare from './Components/Employee-Healthcare/EmployeeHealthcare'
import InsuranceDetail from './Components/Insurance/InsuranceDetail'
import BecomeMember from './Components/OnsurityMembtn/BecomeMember'
import Partners from './Components/OurPartners/Partners'
import PlanType from './Components/PlanList/PlanType';
import FooterComponent from '../../components/Common/Footer/footer'
import Feature from './Components/Feature-Section/Feature'
import ComparisonFaq from './Components/Comparison-Faq/ComparisonFaq'
import { urls } from '../../routes/routes'
import { navigation } from '../../utils/Env'
import { actions as StateActions } from '../../redux/ducks/state_change'
import { bindActionCreators } from 'redux'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'

 function HomePage(props) {

    useEffect(() => {
        props.setNavSelected("")
      }, [])

    return (
        <div>
            <Header />
            <Banner />
            <EmployeeHealthcare />
            <InsuranceDetail />
            <PlanType />
            <BecomeMember 
                 title="Start your comprehensive employee healthcare membership at Just Rs. 145 per month" 
                 value="Become an Onsurity Member"
                 link={urls.VERIFY_PHONE}
                  />
            <Feature />
            <BecomeMember 
                    title="Expand your employees’ benefits beyond insurance at Rs. 145/month" 
                    value="Get Quote"
                    link={urls.VERIFY_PHONE}
                     />
            <Partners />
            <ComparisonFaq />
            <BecomeMember 
                    title="Get customized employee healthcare plan that aligns with your business requirements" 
                    value="Get Quote"
                    link={urls.VERIFY_PHONE}
                     />
            <div style={{width: '85%',margin: 'auto'}}>
               <FooterComponent />
            </div>
        </div>
    )
}

const mapDispatchToProps = (dispatch) => ({
    ...bindActionCreators({
        ...StateActions,
        //ADD IF ITS FROM DIFFRENT ACTION FILE
    }, dispatch)
  });

  export default withRouter(connect(null, mapDispatchToProps)(HomePage))